<template>
  <div id="recommentList" :class="{hasHeader: $utils.showNavBar()}">
    <header-nav v-if="$utils.showNavBar()"></header-nav>
    <div class="MovieDetail">
      <div class="left">
        <div class="cover iconSize">
          <img :src="details.vod_pic" onerror="this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/default_image.png'" alt="封面">
        </div>
      </div>
      <div class="right">
        <div class="detail-title">{{details.vod_name}}</div>
        <div class="tip">{{details.vod_year}}年上映</div>
        <div class="tip"><span>{{details.vod_area}}</span>| <span>{{details.vod_class}}</span> {{details.vod_hits | setFloatNumber}}/次播放</div>
        <div class="tip">导演：{{details.vod_director}}</div>
        <div class="tip">主演：{{details.vod_actor}}</div>
      </div>
    </div>
    <div class="title">{{details.type_id_1 === 1 ? '幕后&影视' : '台前幕后'}}</div>
    <div class="scrollTouch" infinite-scroll-distance="200"  v-infinite-scroll="loadMore">
      <news-list :list="list"></news-list>
      <div class="list-no-data" v-if="loading > 0">{{loading === 1 ? '加载中...' : '没有更多数据！'}}</div>
    </div>
    <div class="back flex-row" @click="$router.go(-1)">←返回看片</div>
  </div>
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
import NewsList from '@/components/common/news-list'
export default {
  components: {
    HeaderNav,
    NewsList
  },
  data () {
    return {
      details: {},
      list: [],
      page: 1,
      loading: 0        // 加载状态
    }
  },
  methods: {
    getDetails () {
      let div = document.createElement('div')
      this.$api.get('MOVIE_DETAIL', {vod_id: this.$route.params.id}, res => {
        this.details = res.data
        div.innerHTML = this.details.vod_content
        this.details.vod_content = div.innerText
        this.details.vod_pic = this.$utils.getPlayUrl(this.details.vod_pic)
      })
    },
    getList (cb) {
      let data = {
        page: this.page,
        vod_id: this.$route.params.id,
        per_page: 12
      }
      this.$api.get('MOVIE_ARTICLE', data, res => {
        this.loading = 0
        cb && cb(res)
        if (this.page >= res.data.last_page) {
          this.loading = 2
        }
        this.page++
      }, res => {
        this.loading = 0
      })
    },
    loadMore () {
      if (this.loading === 1 || this.loading === 2) {
        return
      }
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
      this.loading = 1
      this.timer = setTimeout(() => {
        this.getList(res => {
          this.list = [...this.list, ...res.data.data]
        })
      }, 300)
    }
  },
  mounted () {
    this.getDetails()
    this.getList(res => {
      this.list = res.data.data
    })
  }
}
</script>
<style lang="scss" scoped>
#recommentList{
  height: 100vh;
  overflow: auto;
  box-sizing: border-box;
  padding-bottom: 100px;
  .back {
    position: fixed;
    left: 18px;
    width: 187px;
    height: 68px;
    background: #E3B256;
    box-shadow: 4px 4px 14px 0 #C9A166;
    border-radius: 33.5px;
    padding-top: 14px;
    color: #fff;
    font-size: 30px;
    box-sizing: border-box;
    bottom: 160px;
  }
  .MovieDetail {
    display: flex;
    padding: 50px 20px 40px;
    .left {
      .cover {
        width: 230px;
        height: 320px;
      }
      margin-right: 30px;
    }
    .right {
      flex: 1;
      min-width: 0;
      .detail-title {
        font-weight: 900;
        font-size: 36px;
        line-height: 50px;
        margin-bottom: 30px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }
      .tip {
        margin-bottom: 20px;
        line-height: 40px;
        font-size: 28px;
        color: #999;
        span {
          display: inline-block;
          margin: 0 10px;
          &:first-child {
            margin-left: 0;
          }
        }
      }
    }
  }
  .title {
    font-size: 32px;
    color: #333333;
    padding: 0 30px;
    font-weight: 900;
  }
  .list-no-data {
    line-height: 80px;
    text-align: center;
    color: #999;
    font-size: 28px;
  }
}
</style>



