<template>
<div class="scrollBox">
  <header-nav v-if="$utils.showNavBar()"></header-nav>
  <div id='movieSubScribe' :class="{hasHeader: $utils.showNavBar()}">
    <div class="scrollTouch" v-infinite-scroll="loadMore" infinite-scroll-distance="50">
      <div class="banner">
        <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/web_banner.png" alt="订阅">
      </div>
      <div class="isUpdate flex-row jusB">
        <div class="text">院线大片更新提醒</div>
        <div class="icon iconSize" @click='setSubscribe'>
          <img v-if="is_subscribe" src="../../../assets/img/movie/sub_active.png" alt="">
          <img v-else src="../../../assets/img/movie/sub.png" alt="">
        </div>
      </div>
      <div class="rect"></div>
      <div class="tabsList">
        <div class="tab-content flex-row">
          <div class="tab-item" @click="setActive(index)" :class="{active: index === activeIndex}" v-for="(item, index) in buttons" :key="index">
            <div class="tab-item-text">
              {{item}}
            </div>
          </div>
        </div>
        <div class="list" v-if="list.length > 0">
          <list :list="list" :type="8"></list>
          <div class="no-list-data" v-if="loading > 0">{{loading === 1 ? '加载中...' : '没有更多数据！'}}</div>
        </div>
        <div class="noList" v-if="list.length === 0 && isRequest">
          <div class="no_movie iconSize">
            <img src="../../../assets/img/movie/noList.png" alt="没有列表">
          </div>
          <div class="text">暂无订阅提醒～</div>
        </div>
      </div>
    </div>
  </div>
</div>
  
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
import List from '@/components/movie/common/list'
export default {
  components: {
    HeaderNav,
    List
  },
  data () {
    return {
      isRequest: false,
      is_subscribe: true,
      loading: 0,
      page: 1,
      buttons: ['提醒中', '已结束'],
      list: [],
      activeIndex: 0,
      status: 1
    }
  },
  methods: {
    // 切换订阅
    setActive (index) {
      this.activeIndex = index
      this.page = 1
      this.isRequest = false
      if (index === 0) {
        this.status = 1
      } else if (index === 1) {
        this.status = 2
      }
      this.list = []
      this.getList(res => {
        this.list = res.data.data
      })
    },
    // 获取当前订阅类型
    getCurrentSubscribe () {
      this.$api.get('MOVIE_OPEN_SUBSCRIBE', {}, res => {
        this.is_subscribe = res.data.is_open
      })
    },
    // 添加订阅
    // 取消订阅
    setSubscribe () {
      let txt = '已开启推送'
      let url = ''
      if (this.is_subscribe) {
        txt = '已关闭推送'
        url = 'SUBSCRIBE_DEL'
      } else {
        url = 'SUBSCRIBE_ADD'
      }
      this.$api.post(url, {}, res => {
        this.$toast({
          message: txt
        })
        this.is_subscribe = !this.is_subscribe
      })
    },
    loadMore () {
      if (this.loading > 0) {
        return
      }
      this.loading = 1
      this.getList(res => {
        this.list = [...this.list, ...res.data.data]
      })
    },
    getList (cb) {
      let data = {
        page: this.page,
        status: this.status,
        per_page: 12
      }
      this.$api.get('MOVIE_SUBSCRIBE_LIST', data, res => {
        cb && cb(res)
        this.loading = 0
        this.isRequest = true
        if (this.page >= res.data.last_page) {
          this.loading = 2
        }
        this.page++
      }, res => {
        this.loading = 0
      })
    },
    initData () {
      this.getCurrentSubscribe()
      this.loading = 1
      this.getList(res => {
        this.list = res.data.data
      })
    }
  },
  mounted () {
    this.initData()
  },
  watch: {
    '$store.state.commonState.login_box' (flag) {
      if (!flag && this.$store.state.common.login_bol) {
        this.initData()
      }
    }
  }
}
</script>
<style lang="scss" scoped>
#movieSubScribe {
  height: 100vh;
  box-sizing: border-box;
  overflow: auto;
  padding-bottom: 80px;
  .banner {
    height: 300px;
    img {
      height: 100%;
    }
  }
  .no-list-data {
    margin-top: 20px;
  }
  .rect {
    background: #f5f5f5;
    height: 20px;
  }
  .isUpdate {
    padding: 0 30px;
    height: 140px;
    font-size: 36px;
    color: #333333;
    .icon {
      width: 143px;
      height: 61px;
    }
  }
  .tab-content {
    height: 88px;
    border-bottom: 1px solid #f5f5f5;
    .tab-item {
      flex: 1;
      display: flex;
      justify-content: center;
      .tab-item-text {
        padding: 31px 20px 20px;
        line-height: 35px;
        border-bottom: 4px solid #fff;
        color: #333;
        box-sizing: border-box;
      }
      &.active{
        .tab-item-text {
          color: #F4A82B;
          border-bottom:4px solid #F4A82B;
          padding: 31px 20px 20px;
        }
      }
    }
  }
  .noList {
    padding: 120px 0;
    .iconSize {
      margin: 0 auto 20px;
      width: 115px;
      height: 96px;
    }
    .text {
      line-height: 45px;
      text-align: center;
      color: #999;
    }
  }
}
</style>



